<template>
    <yd-layout>
        <div class="handleTabBox">
            <yd-flexbox>
                <yd-flexbox-item>
                    <div class="tabTopL">绩效</div>
                </yd-flexbox-item>
                <yd-flexbox-item>
                    <div @click="closeWebView" class="tabTopR"><img class="imgWork" src="../../../static/images/home@2x.png">工作中心</div>
                </yd-flexbox-item>
            </yd-flexbox>
            <div class="tabBottom">
                <router-link to="/handle"><span class="tabIcon">待办</span></router-link>
                <router-link to=""><span class="tabIcon active">绩效</span></router-link>
                <router-link to="/meetingManage"><span class="tabIcon">会议</span></router-link>
                <span class="tabIcon" @click="jumpSchedule()">日程</span>
            </div>
        </div>
        <div class=" handleList" v-if="dataList.length==0">
            <div class="noBox">
                <img src="../../../static/images/label/noHandle.png">
            </div>
            <p class="noWord">您暂时没有下属评价</p>
        </div>
        <div class="mainList">
            <div class="onePart twoPart" v-for="(item, index) in dataList" :key="index" @click="selectFlag==false?$router.push('/reviewDetail/'+item.KpiId):$router.push('/understrapper')">
                <div class="personPart" :class="{personPart_BR:selectFlag==true}">
                    <div class="yf_1"><span class="yf_span">个人指标</span></div>
                    <div class="yf_2">{{item.KpiName}} — {{item.UserName}}</div>
                    <div class="yf_4">
                        <div class="fy_4_left" v-if="item.Score!=null">考评成绩：{{item.Score}}</div>
                        <div class="fy_4_right fy_4_right2" :class="{fy_4_right3:item.Score==null}">状态：{{item.Status}}</div>
                    </div>
                </div>
                <div class="appraise" @click="$router.push('/reviewDetail/'+item.KpiId)" v-if="selectFlag==false&&item.Score!=null">评价</div>
                <div class="selImg" v-show="selectFlag==true&&item.Score!=null" ref="selImg" attr="0" @click="getSelect(item.KpiId,index)"></div>
            </div>
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <div class="selectBtn" v-if="selectFlag==false" @click="turnBtn()">选择要提交的个人考评</div>
            <div class="btn_bot1" v-if="selectFlag==true">
                <div class="per_leftBtn" @click="submitData();">提交</div>
                <div class="per_rightBtn" @click="selectFlag=false;">取消</div>
            </div>
            <yd-tabbar-item link="/performanceList" title="个人待办">
                <yd-icon name="grdb2" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item link="" title="下属评价" active>
                <yd-icon name="xspj2" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="我的考评" link="/myPerformance">
                <yd-icon name="wdkp" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            selectFlag: false,
            checkedNum: 1,
            dataList: [],
            kpiIdList: [],
            turnFlag: false
        }
    },
    mounted() {
        document.setTitle('绩效管理');
        let vm = this;
        vm.doList();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        doList() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/DoList/GetLeaderDoList', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.dataList = JSON.parse(res.JsonData).DoEvaluateList.reverse();
                for (let i = 0; i < vm.dataList.length; i++) {
                    if (vm.dataList[i].Score != null) {
                        vm.kpiIdList.push(vm.dataList[i].KpiId);
                    }
                }
                if (vm.kpiIdList.length != 0) {
                    vm.turnFlag = true;
                }
                console.log(vm.kpiIdList);
            });
        },
        getSelect(KpiId, index) {
            let vm = this;
            vm.$refs.selImg[index].attributes.attr.value++;
            if (vm.$refs.selImg[index].attributes.attr.value % 2 == 1) {
                vm.$refs.selImg[index].classList.add('selImg2');
                for (let i = 0; i < vm.kpiIdList.length; i++) {
                    if (KpiId == vm.kpiIdList[i]) {
                        vm.kpiIdList.splice(i, 1);
                    }
                }
            } else {
                vm.$refs.selImg[index].classList.remove('selImg2');
                vm.kpiIdList.push(KpiId);
            }
        },
        submitData() {
            let vm = this;
            if (vm.kpiIdList.length == 0) {
                vm.$dialog.toast({
                    mes: '请选择需要提交的量表',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/MonthlyKpi/KpiEvaluateSubmit', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    KpiIdList: '[' + vm.kpiIdList + ']'
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交成功',
                        timeout: 2000,
                        callback: () => {
                            vm.selectFlag = false;
                            vm.doList();
                        }
                    });
                });
            }
        },
        // 跳转到日程
        jumpSchedule() {
            let vm = this;
            vm.$router.push({
                name: 'mySchedule',
                params: {
                    schId: vm.getUserInfo.empcode,
                    schFlag: 0,
                    schName: 'my'
                }
            });
        },
        turnBtn() {
            let vm = this;
            if (vm.turnFlag == true) {
                vm.selectFlag = true;
            } else {
                vm.$dialog.toast({
                    mes: '无可提交的量表',
                    timeout: 2000
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import './performance.less';
.tabIcon {
    margin-right: 0!important;
}

.yd-icon-grdb2:before,
.yd-icon-xspj2:before,
.yd-icon-wdkp:before {
    display: block;
    content: "";
}

.yd-icon-grdb2:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/performance/grdb2@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
}

.yd-icon-xspj2:before {
    width: 0.5rem;
    height: 0.41rem;
    background: url(../../../static/images/performance/xspj2@2x.png) no-repeat 0 0;
    background-size: 0.5rem;
}

.yd-icon-wdkp:before {
    width: 0.35rem;
    height: 0.35rem;
    background: url(../../../static/images/performance/wdkp.png) no-repeat 0 0;
    background-size: 0.35rem;
}

.fy_4_left {
    // width: 1.8rem!important;
}

.fy_4_right {
    margin-left: 1.2rem!important;
}

.fy_4_right2 {
    margin-left: 2rem!important;
}

.fy_4_right3 {
    margin-left: 0rem!important;
}

.fr_span {
    color: #ff4677!important;
}

.yf_1 {
    padding-top: .06rem!important;
}

.twoPart {
    padding-top: .2rem!important;
    padding-bottom: .2rem!important;
}

.per_rightBtn {
    background-color: #ff4677!important;
}
</style>


